.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 20px;
}

body {
  font-family: "Lucida Grande", "Lucida Sans", Arial, sans-serif;
  background: rgb(247, 183, 192);
  background: linear-gradient(
    90deg,
    rgba(247, 183, 192, 1) 0%,
    rgba(237, 122, 157, 1) 35%,
    rgba(242, 138, 173, 1) 100%
  );
}

.lyrics-wrap {
  display: flex;
  flex-direction: column;
  align-items: start;
}

.sign-off-container {
  padding: 2px 0 0 50%;
  display: flex;
  flex-direction: column;
  align-items: start;
}

.sign-off {
  padding-left: 25px;
  padding-top: 2px;
}

p {
  margin-top: 2px;
  padding: 0 10px 0 10px;
  text-align: left;
}

h1 {
  font-size: 1em;
  padding-top: 10px;
  padding-bottom: 1px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
